.main-nav {
    float: right;
    margin-right: 5%;
    width: 100%;
    height: 100%;
    cursor: pointer;
    & ul {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        -webkit-transform: translateY(-100%);
        -moz-transform: translateY(-100%);
        -ms-transform: translateY(-100%);
        -o-transform: translateY(-100%);
        transform: translateY(-100%);
        & .is-visible {
            -webkit-transform: translateY(50px);
            -moz-transform: translateY(50px);
            -ms-transform: translateY(50px);
            -o-transform: translateY(50px);
            transform: translateY(50px);
        }
    }
    & a {
        display: block;
        height: 50px;
        line-height: 50px;
        padding-left: 5%;
        background: #292a34;
        border-top: 1px solid #3b3d4b;
        color: #fff;
    }
}

@media only screen and (min-width: 768px) {
    .main-nav {
        width: auto;
        height: auto;
        background: none;
        cursor: auto;
        & ul {
            position: static;
            width: auto;
            -webkit-transform: translateY(0);
            -moz-transform: translateY(0);
            -ms-transform: translateY(0);
            -o-transform: translateY(0);
            transform: translateY(0);
            line-height: 80px;
            &.is-visible {
                -webkit-transform: translateY(0);
                -moz-transform: translateY(0);
                -ms-transform: translateY(0);
                -o-transform: translateY(0);
                transform: translateY(0);
            }
        }
        & li {
            display: inline-block;
            margin-left: 1em;
            &:nth-last-child(2) {
                margin-left: 2em;
            }
        }
        & a {
            display: inline-block;
            height: auto;
            line-height: normal;
            background: transparent;
        }
    }
}
.cd-user-modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(52, 54, 66, 0.9);
    z-index: 3;
    overflow-y: auto;
    cursor: pointer;
    visibility: hidden;
    opacity: 0;
    -webkit-transition: opacity 0.3s, visibility 0.3s;
    -moz-transition: opacity 0.3s, visibility 0.3s;
    transition: opacity 0.3s, visibility 0.3s;
    &.is-visible {
        visibility: visible;
        opacity: 1;
        & .cd-user-modal-container {
            -webkit-transform: translateY(0);
            -moz-transform: translateY(0);
            -ms-transform: translateY(0);
            -o-transform: translateY(0);
            transform: translateY(0);
        }
    }
}
.cd-user-modal-container {
    position: relative;
    width: 90%;
    max-width: 600px;
    background: #fff;
    margin: 3em auto 4em;
    cursor: auto;
    border-radius: 0.25em;
    -webkit-transform: translateY(-30px);
    -moz-transform: translateY(-30px);
    -ms-transform: translateY(-30px);
    -o-transform: translateY(-30px);
    transform: translateY(-30px);
    -webkit-transition-property: -webkit-transform;
    -moz-transition-property: -moz-transform;
    transition-property: transform;
    -webkit-transition-duration: 0.3s;
    -moz-transition-duration: 0.3s;
    transition-duration: 0.3s;
    & .cd-switcher {
        &:after {
            content: "";
            display: table;
            clear: both;
        }
        & li {
            width: 50%;
            float: left;
            font-family: "SF UI Text";
            font-style: normal;
            font-weight: 500;
            font-size: 18px;
            line-height: 29px;
            text-align: center;
            color: #171717;
            &:first-child a {
                border-radius: 0.25em 0 0 0;
            }
            &:last-child a {
                border-radius: 0 0.25em 0 0;
            }
        }
        & a {
            display: block;
            width: 100%;
            height: 50px;
            line-height: 50px;
            background: #d2d8d8;
            color: #809191;
            &.selected{
                background: #fff;
                color: #505260;
            }
        }
    }
}

@media only screen and (min-width: 600px) {
    .cd-user-modal-container {
        margin: 4em auto;
        & .cd-switcher a {
            height: 70px;
            line-height: 70px;
        }
    }
}

.cd-form {
    padding: 1.4em;
    & .fieldset {
        position: relative;
        margin: 1.4em 0;
        &:first-child {
            margin-top: 0;
        }
        &:last-child {
            margin-bottom: 0;
        }
    }
    & label {
        font-size: 14px;
        font-size: 0.875rem;
        &.image-replace {
            display: inline-block;
            position: absolute;
            left: 15px;
            top: 50%;
            bottom: auto;
            -webkit-transform: translateY(-50%);
            -moz-transform: translateY(-50%);
            -ms-transform: translateY(-50%);
            -o-transform: translateY(-50%);
            transform: translateY(-50%);
            height: 20px;
            width: 20px;
            overflow: hidden;
            text-indent: 100%;
            white-space: nowrap;
            color: transparent;
            text-shadow: none;
            background-repeat: no-repeat;
            background-position: 50% 0;
        }
        &.cd-username {
            background-image: url("../img/modal/username.svg");
        }
        &.cd-email {
            background-image: url("../img/modal/email.svg");
        }
        &.cd-password {
            background-image: url("../img/modal/password.svg");
        }
        &.cd-login {
            background-image: url("../img/modal/login.png");
        }
    }
    & input {
        margin: 0;
        padding: 0;
        border-radius: 0.25em;
        &.fieldset-input {
            width: 100%;
            padding: 12px 20px 12px 50px;
            border: 1px solid #d2d8d8;
            -webkit-appearance: none;
            -moz-appearance: none;
            -ms-appearance: none;
            -o-appearance: none;
            appearance: none;
        }
        &[type="password"] {
            padding-right: 65px;
        }
        &[type="submit"] {
            padding: 16px 0;
            cursor: pointer;
            background: var(--button-hover-color);
            border: 2px solid var(--button-hover-color);
            box-sizing: border-box;
            border-radius: 8px;
            font-family: "Roboto";
            font-style: normal;
            font-weight: bold;
            font-size: 18px;
            line-height: 15px;
            text-align: center;
            color: #fefefe;
            border: none;
            -webkit-appearance: none;
            -moz-appearance: none;
            -ms-appearance: none;
            -o-appearance: none;
            appearance: none;
        }
    }
    & .hide-password {
        display: inline-block;
        position: absolute;
        right: 0;
        top: 0;
        padding: 6px 15px;
        border-left: 1px solid #d2d8d8;
        top: 50%;
        bottom: auto;
        -webkit-transform: translateY(-50%);
        -moz-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
        -o-transform: translateY(-50%);
        transform: translateY(-50%);
        font-size: 14px;
        font-size: 0.875rem;
        color: #343642;
    }
}
.no-touch .cd-form input[type="submit"]:hover,
.no-touch .cd-form input[type="submit"]:focus {
    background: var(--menu-background-color);
    outline: none;
}
@media only screen and (min-width: 600px) {
    .cd-form {
        padding: 2em;
        &.fieldset {
            margin: 2em 0;
            &:first-child {
                margin-top: 0;
            }
            &:last-child {
                margin-bottom: 0;
            }
        }
        & input[type="submit"] {
            padding: 16px 0;
        }
    }
}
.cd-close-form {
    display: block;
    position: absolute;
    width: 40px;
    height: 40px;
    right: 0;
    top: -40px;
    background: url("images/close.svg") no-repeat center center;
    text-indent: 100%;
    white-space: nowrap;
    overflow: hidden;
}
@media only screen and (min-width: 1170px) {
    .cd-close-form {
        display: none;
    }
}

#cd-login,
#cd-signup {
    display: none;
}

#cd-login.is-selected,
#cd-signup.is-selected {
    display: block;
}