.item {
  position: relative;
  margin: 0;
  margin-bottom: 20px;
  border-bottom: 1px solid #D8E3FE;
  align-items: flex-start;
  max-width: 750px;
}

.item-text {
  display: none;
  max-width: 400px;
  font-size: 15px;
  margin: 30px auto;
}

.item-check {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  width: 100%;
  height: 100%;
  justify-items: start;
}

.item-title {
  padding: 15px;
  display: block;
  cursor: pointer;
}

.item-title::before {
  content: '';
  position: absolute;
  left: 50px;
  top: 25px;
  border: 5px solid transparent;
  border-top: 5px solid #8E96B7;
  margin-right: 30px;
}

.item-check:checked ~ .item-text {
  display: block;
}

.item-check:checked ~ .item-title {
  color: #495274;
}

.item-check:checked ~ .item-title::before {
  content: '';
  border: 5px solid transparent;
  border-bottom: 5px solid #8E96B7;
}

