  position: absolute;
  width: 100%;
  height: 2px;
  background-color: #000;
  border-radius: 76px;
}

.menu__btn > span::before {
  content: "";
  top: -8px;
}

.menu__btn > span::after {
  content: "";
  top: 8px;
}

.menu__box {
  display: block;
  position: fixed;
  visibility: hidden;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 999;
  margin: 0;
  padding: 0 0;
  text-align: left;
  font-family: "SF UI Text";
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 100%;
  color: #ffffff;
  background: #171717;
  box-shadow: 1px 0px 6px rgba(0, 0, 0, 0.4);
  transition-duration: 0.25s;
}

.menu__box li {
  margin-bottom: 2%;
}

.menu__box li a {
  display: flex;
  align-items: center;
}

.menu__box li a img {
  margin-right: 25px;
}

.menu__box li:first-child {
  padding-top: 15px;
  padding-left: 15px;
  margin-bottom: 30px;
}

.menu__box li:last-child {
  position: absolute;
  bottom: 15px;
  width: 100%;
}

.menu__item {
  display: block;
  padding: 15px;
  color: #fff;
  font-size: 20px;
  font-weight: 600;
  text-transform: uppercase;
  transition-duration: 0.25s;
  cursor: pointer;
}

.menu__item:hover {
  background-color: #2fdac5;
}

.login-link {
  display: none;
}

#menu__toggle:checked ~ .menu__btn > span {
  background: #fff;
  position: absolute;
  left: 300px;
  top: 10px;
  transform: translate(0, 0) rotate(45deg);
}

#menu__toggle:checked ~ .menu__btn > span::before {
  top: 0;
  position: absolute;
  background: #fff;
  transform: translate(0px, 0) rotate(0);
}

#menu__toggle:checked ~ .menu__btn > span::after {
  top: 0;
  background: #fff;
  position: absolute;
  transform: translate(0px, 0) rotate(90deg);
}

#menu__toggle:checked ~ .menu__box {
  visibility: visible;
  right: 0;
}

.menu__btn > span,
.menu__btn > span::before,
.menu__btn > span::after {
  transition-duration: 0.25s;
}

